import { mapState } from 'vuex';
import { addNotice } from '@/request/API/index.js';
export default {
	data() {
		return {
			img3: [],
			title: '',
			content: '',
			imageId: '',
			phone: ''
		};
	},
	onLoad() {},
	computed: {
		...mapState(['userId', 'shopId'])
	},
	methods: {
		fabu() {
			if (!this.imageId) {
				uni.showModal({
					title: '',
					content: '请上传公告图片',
					showCancel: false
				});
				return;
			}
			if (!this.title) {
				uni.showModal({
					title: '',
					content: '请输入公告标题',
					showCancel: false
				});
				return;
			}
			if (!this.content) {
				uni.showModal({
					title: '',
					content: '请输入公告内容',
					showCancel: false
				});
				return;
			}
			if (this.phone) {
				const reg = /^[1][3,4,5,7,8][0-9]{9}$/;
				if (!reg.test(this.phone)) {
					uni.showModal({
						title: '',
						content: '请输入正确的手机号码',
						showCancel: false
					});
					return;
				}
			}

			let params = {
				title: this.title,
				content: this.content,
				imageId: this.imageId,
				shopId: this.shopId,
				userId: this.userId,
				phone: this.phone,
				noticeType: 0
			};
			addNotice(params).then(res => {
				if (res.data.code == 0) {
					uni.showToast({
						title: '公告发布成功',
						icon: 'none',
						duration: 1000
					});
					setTimeout(res => {
						uni.navigateBack({
							data: 1
						});
					}, 1500);
				} else {
					uni.showToast({
						title: '发布失败',
						icon: 'none',
						duration: 1000
					});
				}
			});
		},

		paizhao3() {
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album', 'camera'], //从相册选择
				success: res => {
					this.img3 = res.tempFilePaths;
					uni.uploadFile({
						url: baseURL + '/file/upload',
						filePath: res.tempFilePaths[0],
						name: 'file',
						formData: {
							user: 'test',
							file: res.tempFilePaths[0]
						},
						success: uploadFileRes => {
							let aaa = JSON.parse(uploadFileRes.data);
							if (aaa.code == 0) {
								console.log(uploadFileRes);
								this.imageId = aaa.data;
								uni.showToast({
									title: '上传成功'
								});
							}
						}
					});
				}
			});
		}
	}
};
